import { createApp } from 'vue'
import App from './App.vue'
import * as VueRouter from 'vue-router'
import "./main.css"
import ShoppingCartPage from "./pages/ShoppingCartPage.vue"
import ProductsPage from "./pages/ProductsPage.vue"
import ProductDetailPage from "./pages/ProductDetailPage.vue"
import NotFoundPage from "./pages/NotFoundPage.vue"


import { initializeApp } from "firebase/app";
// import { getAnalytics } from "firebase/analytics";

const firebaseConfig = {
  apiKey: "AIzaSyDHLcxwiTQplpQbcL2_ioBkiNuVGD6Vg6I",
  authDomain: "learn-vue-75d0a.firebaseapp.com",
  projectId: "learn-vue-75d0a",
  storageBucket: "learn-vue-75d0a.appspot.com",
  messagingSenderId: "858359861171",
  appId: "1:858359861171:web:377a654650cfc9f68ac5c6",
  measurementId: "G-FQY7V9D7V4"
};


initializeApp(firebaseConfig);
// const analytics = getAnalytics(app);


createApp(App)
.use(VueRouter.createRouter({
    history: VueRouter.createWebHistory(process.env.BASE_URL),
    routes:[
        {
            path:"/cart",
            component:ShoppingCartPage
        },
        {
            path:"/products",
            component:ProductsPage
        },
        {
            path:"/products/:productId",
            component:ProductDetailPage
        },{
            path:'/',
            redirect:'/products'
        },
        {
            path:"/:pathMatch(.*)*",
            component:NotFoundPage
        }
    ]
}))
.mount('#app')
